<template>
    <el-container>
        <el-main>
            <div class="main-part">
                <div class="left-item">
                    <event-list-day ref="eventListDay" @synchronousData="synchronousData"/>
                </div>
                <div class="right-item">
                    <event-list-long ref="eventListLong"/>
                </div>
            </div>
        </el-main>
    </el-container>
</template>

<script>
    import * as DateUtil from '../assets/js/DateUtil'
    import apis from '../apis/EventList'
    import EventListDay from '../components/eventList/EventListDay'
    import EventListLong from '../components/eventList/EventListLong'

    export default {
        name: "EventList",
        components: {
            EventListDay,
            EventListLong
        },
        methods: {
            synchronousData() {
                if (sessionStorage.getItem("isLogin")) {
                    console.log('login in');
                    //日数据
                    let sendDataDay = {
                        type: 'DAY',
                        tag: DateUtil.getTimestamp('yyyymmdd', this.pickedDate),
                        data: this.$refs.eventListDay.eventListDay
                    };
                    //周数据
                    let sendDataWeek = {
                        type: 'WEEK',
                        tag: DateUtil.getWeekOfYear(this.pickedDate),
                        data: this.$refs.eventListLong.dataListWeek
                    };

                    let sendData = {
                        sendDataDay: sendDataDay,
                        sendDataWeek: sendDataWeek
                    };
                    //调用api接口
                    apis.updateData(sendData).then(res => {
                        let result = res.data.result;
                        console.log(result);
                        if (result == 'success') {
                            this.$message.success(result);
                        } else {
                            this.$message.error(result);
                        }
                    }).catch(error => {
                        console.log(error);
                        this.$message.error('Operate Error')
                    });
                } else {
                    //未登录
                    console.log('not login')
                    let keyDay = DateUtil.getTimestamp('yyyymmdd', this.pickedDate);         //存数据的key值
                    let dataDay = this.eventListDay;                          //将被存的数据，也可以字符串

                    //同步日数据
                    IndexDB.save('kaolaDB', 'event_list', keyDay, dataDay, function (result) {
                        if (result.code == 'success') {
                            console.log(result.code)
                        } else {
                            console.error(result)
                        }
                    });

                    //同步周数据
                    let keyWeek = DateUtil.getWeekOfYear(this.pickedDate);
                    let dataWeek = this.$store.state.eventList.eventListWeek;
                    IndexDB.save('kaolaDB', 'event_list', keyWeek, dataWeek, function (result) {
                        if (result.code == 'success') {
                            console.log(result.code)
                        } else {
                            console.error(result)
                        }
                    });
                }
            }
        }
    }
</script>

<style scoped>
    .el-main {
        text-align: center;
    }

    .main-part {
        position: relative;
        width: 100%;
        height: 600px;
        overflow: hidden;

        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: flex-start;
    }

    .main-part .left-item {
        position: relative;
        width: 560px;
        height: 100%;

        background: rgba(255, 255, 255, 0.3);
    }

    .main-part .right-item {
        flex: 1;
        height: 100%;
        margin-left: 1%;

        background: rgba(255, 255, 255, 0.3);
    }

    .event-box {
        height: 50%;
    }

    .padding-0 {
        padding: 0;
    }
</style>



