import instance from '../axios';

export default {
    //用户注册
    userRegister(data) {
        return instance.post('/register_new', data);
    },
    //用户登录
    userLogin(data) {
        return instance.post('/user/login', data);
    },
    //获取用户
    getUser(data) {
        return instance.get('/user/getByName',  {params:data});
    },
    //删除用户
    delUser(data) {
        return instance.post('/delUser', data);
    }
}




