<template>
    <div class="docker-full">
        <el-container class="height-50">
            <el-main>
                <img class="full-img" :src="pictureInfo.firstItem.src"
                     :title="pictureInfo.firstItem.title"/>
            </el-main>
            <el-main>
                <p>{{pictureInfo.secondItem.desc}}</p>
            </el-main>
        </el-container>
        <el-container class="height-50">
            <el-main>
                <p>{{pictureInfo.thirdItem.desc}}</p>
            </el-main>
            <el-main>
                <img class="full-img" :src="pictureInfo.fourthItem.src"
                     :title="pictureInfo.fourthItem.title"/>
            </el-main>
        </el-container>
    </div>
</template>

<script>
    import DemoData from '../../assets/demoData/main/NavPicDescData'

    export default {
        name: 'NavPicDesc',
        data() {
            return {
                pictureInfo: DemoData.picture_1
            }
        },
        computed: {
            pictureId() {
                return this.$route.params.id;
            }
        },
        watch: {
            pictureId: function (val, oldVal) {
                console.log(val, oldVal);
                console.log(DemoData['picture_1']);
                if (sessionStorage.getItem("isLogin")) {
                    console.log("logined");
                } else {
                    if (DemoData[val]) {
                        let pictureInfo = DemoData[val];
                        this.pictureInfo = pictureInfo;
                    }
                }
            }
        }
    }
</script>

<style lang="less" scoped>
    .height-50 {
        height: 50%;
    }

    .el-main {
        padding: 0;
        width: 50%;
        text-align: left;
        text-indent: 35px;
        /*background: yellow;*/

        p {
            text-indent: 2em;
        }

        .full-img {
            display: block;
            width: 100%;
            height: 100%;
        }
    }
</style>



