import {userMutations} from '../mutation-types'

const user = {
    namespaced: true,   // 默认情况下，模块内部的 action、mutation 和 getter 是注册在全局命名空间的
    state: {
        token: '',      // 模块内的状态已经是嵌套的了，使用 `namespaced` 属性不会对其产生影响this.$store.state.user.token
        role: 'user'
    },
    mutations: {
        [userMutations.SET_TOKEN]: (state, payload) => {               // commit('user/set_token')
            // console.log('set_token:', payload);
            state.token = payload.token
        },
        [userMutations.SET_ROLE]: (state, payload) => {               // commit('user/set_role')
            state.role = payload.role
        }
    },
    actions: {
        getRole({commit, state}, payload) {
            console.log('start');
            let role = payload.role;
            setTimeout(() => {
                console.log('commit');
                role = 'boss';
                //调用后台apis(发送参数，成功回调，失败回调)
                commit({
                    type: userMutations.SET_ROLE,
                    role: role
                });
                console.log(state.role);
            }, 2000)
        }
    }
};

export default user;



