<template>
    <div class="log-docker"
         :style="{'left': positionInfo.clientX-100 + 'px','top': positionInfo.clientY+60 + 'px'}">
        <i class="el-icon-close close-btn" @click="hideLogDiv"/>
        <div>who am i:{{getRole}}</div>
        <div>who am i:{{role}}</div>
    </div>
</template>

<script>
    export default {
        name: "LogInMini",
        props: {
            positionInfo: {
                type: Object,
                required: false,
                default: {
                    clientX: 100,
                    clientY: 100
                }
            }
        },
        data() {
            return {
                role: this.$store.state.user.role       //不使用计算属性，则数据无法实时更新
            }
        },
        methods: {
            hideLogDiv() {
                this.$emit('hideLogDiv');
            }
        },
        mounted() {
            // 以对象形式分发
            this.$store.dispatch({
                type: 'user/getRole',
                role: 'admin'
            })
        },
        computed: {
            getRole() {
                return this.$store.state.user.role;
            }
        }
    }
</script>

<style lang="less" scoped>
    @log-div-width: 300px;
    @log-div-height: 200px;

    .log-docker {
        position: absolute;
        width: @log-div-width;
        height: @log-div-height;
        background: lightcyan;
        z-index: 99;

        .close-btn {
            position: absolute;
            right: 2%;
            top: 2%;
            color: #2299DD;
            font-size: 0.5rem;
            cursor: pointer;
        }
    }
</style>



