[图片放大缩小](https://segmentfault.com/q/1010000012038536)
[ElementUI](https://www.jb51.net/article/142638.htm)
<template>
    <div class="container">
        <div class="pic_div" v-for="(img,index) in imgList" :key="img.id">
            <img :src="img.src" :title="img.title" @click="showDetail(img.router, img.id)">
        </div>
    </div>
</template>

<script>
    import {picBoxData} from '../../assets/demoData/main/NavPicBoxData'

    export default {
        name: "NavPicBox",
        data() {
            return {
                imgList: picBoxData
            }
        },
        methods: {
            showDetail(router, param) {
                if (router && param) {
                    this.$router.push({name: router, params: {id: param}})
                } else {
                    this.$router.push({name: 'pic-info', params: {id: param}})
                }
            }
        }
    }
</script>

<!--图片自适应-->
<!--1.只需通过css设置background-size属性为contain，即-->
<!--background-size:contain-->
<!--2.如下-->
<!--https://blog.csdn.net/zh_rey/article/details/69666232-->
<style lang="less" scoped>
    .container {
        position: relative;
        width: 100%;
        height: 600px;
        margin: 0;
        /*border: 1px solid blue;*/

        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-items: center;
        align-content: flex-start;

        .pic_div {
            width: 132px;
            height: 90px;
            margin: 4px 0 0 4px;

            transform: scale(1);
            transition: all ease 0.2s;

            &:hover {
                transform: scale(1.5);
                z-index: 10;
            }

            img {
                position: relative;
                width: 100%;
                height: 100%;
            }
        }
    }
</style>



