<template>
    <div class="docker-star">
        <div class="ask"><a target="_blank">房产契税怎样计算？</a></div>
        <a class="info" target="_blank">
            <div class="info-inner">
                <div class="portrait"><img
                    src="/static/img/main/hubaochao.png">
                    <div class="cover"></div>
                </div>
                <p class="name">胡保朝</p>
                <p class="field">擅长领域：财务</p></div>
        </a></div>
</template>

<script>
    export default {
        name: "StarPeople"
    }
</script>

<style lang="less" scoped>
    @docker-width: 270px;

    .docker-star {
        position: relative;
        width: @docker-width;
        height: 343px;
        background: #FFF;
        border: 1px solid #e9e9e9;

        .ask {
            display: table-cell;
            width: @docker-width;
            height: 110px;
            line-height: 110px;
            text-align: center;
            font-size: 18px;
            color: #353535;
            vertical-align: middle;

            a {
                color: #353535;
                padding: 0 21px;
                line-height: 28px;
                display: -moz-inline-box;
                display: inline-block;
                vertical-align: middle;
                text-decoration: none;
            }
            a:hover {
                color: #35b558;
            }
        }

        .info {
            position: relative;
            display: block;
            width: 100%;
            height: 233px;
            text-align: center;
            text-decoration: none;
            overflow: hidden;
            background: #f4fef6;

            .info-inner {
                width: 100%;
                height: 233px;
                margin: 0 auto;
                overflow: hidden;
                background: url("/static/img/main/module_bg.png") -198px -1292px no-repeat;

                .portrait {
                    position: relative;
                    width: 100px;
                    height: 100px;
                    margin: 37px auto 27px;

                    img {
                        display: block;
                        width: 100px;
                        height: 100px;
                        line-height: 100%;
                    }

                    .cover {
                        position: absolute;
                        width: 137px;
                        height: 116px;
                        top: 0;
                        left: -19px;
                        background: url("/static/img/main/module_bg.png") -309px -1528px no-repeat;
                    }
                }

                .name {
                    font-size: 18px;
                    color: #353535;
                    margin-bottom: 2px;
                }
                .field {
                    color: #35b558;
                }
            }
        }
    }
</style>



