<template>
    <div class="docker-video-board">
        <div class="head line">
            <span class="title">视频专区</span>
        </div>
        <div class="list">
            <div class="videoQuesAsk-left">
                <div class="mainQues">
                    <a href="javascript:void(0)">
                        <div class="videoPic"></div>
                        <div class="askTitle">
                            <span class="title">手机充电有哪些常见误区？</span>
                            <span class="time">03:21</span>
                            <span class="detail">在日常生活中我们不难发现，手机在用了一段时间后，电池就会变得，，，，非常不耐用。</span>
                            <img src="/static/img/main/videoBtn.png" class="btn">
                        </div>
                    </a>
                </div>
                <div class="question">
                    <a href="javascript:void(0)" class="title">如何使用公积金？</a>
                    <span class="time">01:00</span>
                </div>
                <div class="question">
                    <a href="javascript:void(0)" class="title">异地恋我们应该怎么谈？</a>
                    <span class="time">01:00</span>
                </div>
                <div class="question">
                    <a href="javascript:void(0)" class="title">Tomorrow is another day？</a>
                    <span class="time">99:99</span>
                </div>
            </div>
            <div class="videoQuesAsk-right">
                <div class="questions">
                    <a href="javascript:void(0)">
                        <div class="videoPic1"></div>
                        <div class="bg"></div>
                        <img src="/static/img/main/videoBtn.png" class="btn">
                    </a>
                </div>
                <div class="small-title">
                    <span class="title">为啥北方人爱用纸币，南方人爱用硬币？</span>
                    <span class="time">03:01</span>
                </div>
                <div class="questions">
                    <a href="javascript:void(0)">
                        <div class="videoPic2"></div>
                        <div class="bg"></div>
                        <img src="/static/img/main/videoBtn.png" class="btn">
                    </a>
                </div>
                <div class="small-title">
                    <span class="title">中国人怎么什么都吃？</span>
                    <span class="time">02:28</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "VideoBoard"
    }
</script>

<style lang="less" scoped>
    @pic-main-width: 414px;
    @pic-main-height: 224px;

    .docker-video-board {
        position: relative;
        width: 604px;
        height: 420px;
        background: #FFFFFF;

        .head {
            padding-top: 6px;
            padding-bottom: 4px;
            border-bottom: 1px solid #e9e9e9;
            text-align: left;

            .title {
                font-size: 18px;
                color: #000;
                position: relative;
                top: 1px;
                margin-left: 15px;
            }
        }

        .list {
            width: 100%;
            height: 360px;
            text-align: left;
            margin-top: 25px;
            display: flex;

            .videoQuesAsk-left {
                width: 414px;
                height: 360px;
                margin-right: 20px;

                .mainQues {
                    position: relative;
                    width: @pic-main-width;
                    height: @pic-main-height;

                    .videoPic {
                        width: 100%;
                        height: 100%;
                        background-size: cover;
                        background-image: url("/static/img/main/shoujichongdian.png");
                    }

                    .askTitle {
                        position: absolute;
                        color: #FFFFFF;
                        bottom: 0;
                        width: @pic-main-width;
                        height: 60px;
                        background: rgba(0, 0, 0, 0.4);
                        line-height: 30px;
                        padding-left: 12px;
                        padding-right: 16px;
                        padding-top: 10px;
                        box-sizing: border-box;
                        overflow: hidden;

                        .title {
                            display: block;
                            font-size: 18px;
                            overflow: hidden;
                            text-overflow: ellipsis;
                            white-space: nowrap;
                            line-height: 1;
                        }
                        .time {
                            font-size: 12px;
                            margin-right: 18px;
                            line-height: 1;
                        }
                        .detail {
                            display: inline-block;
                            width: 255px;
                            font-size: 12px;
                            overflow: hidden;
                            text-overflow: ellipsis;
                            white-space: nowrap;
                            line-height: 1;
                        }
                        .btn {
                            position: absolute;
                            top: 10px;
                            right: 16px;
                            padding: 0;
                            cursor: pointer;
                            display: inline-block;
                            text-align: center;
                            border: 1px solid transparent;
                            border-radius: 2px;
                        }
                    }
                }
                .question {
                    width: @pic-main-width;
                    line-height: 1;
                    margin: 20px 0;

                    a {
                        float: left;
                        color: #333;
                        display: block;
                        max-width: 374px;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                        text-decoration: none;
                    }
                    .time {
                        /*font-size: 12px;*/
                        margin-left: 10px;
                        color: #8b8b8b;
                    }
                    .title:hover {
                        color: #35b558;
                    }
                }
            }
            .videoQuesAsk-right {
                width: 170px;
                height: 360px;

                .questions {
                    width: 170px;
                    position: relative;

                    .bg {
                        display: none;
                        position: absolute;
                        top: 0;
                        width: 168px;
                        height: 94px;
                        background: rgba(0, 0, 0, 0.4);
                    }
                    .btn {
                        display: none;
                        position: absolute;
                        top: 27px;
                        right: 64px;
                        cursor: pointer;
                        text-align: center;
                        border: 1px solid transparent;
                        border-radius: 2px;
                    }
                    .videoPic1 {
                        width: 160px;
                        height: 94px;
                        margin-bottom: 10px;
                        background-image: url("/static/img/main/zhibi.png");
                        background-size: cover;
                    }
                    .videoPic2 {
                        width: 160px;
                        height: 94px;
                        margin-bottom: 10px;
                        background-image: url("/static/img/main/shucai.png");
                        background-size: cover;
                    }
                }
                /*hover小技巧*/
                /*https://blog.csdn.net/u011511086/article/details/78676111/*/
                .questions:hover .bg {
                    display: block;
                }
                .questions:hover .btn {
                    display: block;
                }

                .small-title {
                    width: 160px;
                    margin-bottom: 28px;
                    word-wrap: break-word;

                    .time {
                        color: #8b8b8b;
                    }
                }
            }
        }
    }
</style>



