import {bookInfoMutations} from '../mutation-types'

const bookInfo = {
    namespaced: true,   // 默认情况下，模块内部的 action、mutation 和 getter 是注册在全局命名空间的
    state: {
        bookId: ''       // 模块内的状态已经是嵌套的了，使用 `namespaced` 属性不会对其产生影响this.$store.state.user.token
    },
    mutations: {
        [bookInfoMutations.SET_BOOKID]: (state, payload) => {               // commit('user/set_token')
            state.bookId = payload.bookId
        }
    }
}

export default bookInfo;



